.container {
  width: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px 20px;
  border: 1px dotted gray;
}

.app-name {
  text-align: center;
}

.balance {
  margin-bottom: 6px;
}

.balance .caption {
  text-transform: uppercase;
  font-size: 10px;
}

.balance .amount {
  font-size: 19px;
}

.income-expense {
  padding: 12px 12px;
  display: flex;
}

.income-expense div {
  flex: 1;
  text-align: center;
  text-transform: uppercase;
}

.income-expense .income {
  border-right: 1px grey dotted;
}

.income-expense > div > div:nth-child(1) {
  font-size: 10px;
}

.income-expense > div > div:nth-child(2) {
  font-size: 16px;
}

.income-expense .income div:nth-child(2) {
  color: rgb(16, 180, 16);
}

.income-expense .expense div:nth-child(2) {
  color: rgb(151, 45, 45);
}

.history h5 {
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px rgb(211, 205, 205) solid;
}

.history .transaction {
  position: relative;
  padding: 6px 12px;
  font-size: 12px;
  border-right: 3px rgb(16, 180, 16) solid;
  margin-bottom: 6px;
}

.history .no-transaction {
  color: red;
  padding: 0 0 8px 0;
  border-bottom: 1px rgb(211, 205, 205) solid;
}

.history .transaction span:nth-child(2) {
  float: right;
}

.history .transaction button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -15px;
  height: 90%;
  width: 20px;
  text-align: center !important;
  background-color: red;
  color: #fff;
  border: none;
  border-radius: 2px;
  display: none;
}

.history .transaction:hover button {
  display: block;
}

.add-new h5 {
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px rgb(211, 205, 205) solid;
}

form .form-group label {
  display: block;
  font-size: 12px;
}

form > div:nth-child(3) label {
  display: inline;
  margin-right: 6px;
}

form .form-group input[type="text"],
form .form-group input[type="number"] {
  width: 100%;
  margin: 5px auto;
  padding: 5px;
  border-radius: 3px;
  border: 1px grey solid;
}

form > input[type="submit"] {
  width: 100%;
  margin-top: 10px;
  font-size: 12px;
  background-color: rgb(209, 27, 209);
  padding: 8px;
  color: #fff;
  outline: none;
  border: none;
}

.rpy {
  padding: 5px 0;
}

.rpx {
  padding: 0 8px;
}

.card {
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.red {
  color: rgb(151, 45, 45);
}

.green {
  color: rgb(16, 180, 16);
}
